.ReactModal__Content {
    max-width: 400px;
    width: 100%;
    max-height: 400px;
    inset: 0 !important;
    left: 50% !important;
    /* transform: translateX(-50%) !important; */
    top: 50% !important;
    transform: translate(-50%, -50%);
    padding-top: 0px !important;
}


.modal-close-button {
    position: absolute;
    top: 10px;
    right: 10px;
    width: 20px;
    height: 20px;
    opacity: 0.7; /* Полупрозрачность */
    cursor: pointer;
    border: none;
    background: none;
  }
  
  .modal-close-button:hover {
    opacity: 1; /* Полная непрозрачность при наведении */
  }
  
  .modal-close-button::before,
  .modal-close-button::after {
    position: absolute;
    left: 50%;
    top: 50%;
    content: ' ';
    height: 20px;
    width: 2px;
    background-color: #000000; /* Цвет крестика */
    transform: translate(-50%, -50%) rotate(45deg); /* Поворот на 45 градусов */
  }
  
  .modal-close-button::after {
    transform: translate(-50%, -50%) rotate(-45deg); /* Поворот на -45 градусов */
  }

  .qr {
    width: 100%;
    height: 100%;
  }
